import React, { useState } from "react";
import { Link } from "react-router-dom";
import Drawer from "@material-ui/core/Drawer";
import Box from "@material-ui/core/Box";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Avatar from "@material-ui/core/Avatar";
import Divider from "@material-ui/core/Divider";
import Typography from "@material-ui/core/Typography";
import MenuIcon from '@material-ui/icons/Menu';
import AssignmentInd from "@material-ui/icons/AssignmentInd";
import Home from "@material-ui/icons/Home";
import Apps from "@material-ui/icons/Apps";
import Button from '@material-ui/core/Button';
import ContactMail from "@material-ui/icons/ContactMail";
import SubjectIcon from '@material-ui/icons/Subject';
import { makeStyles } from "@material-ui/core/styles";
import avatar from "../avatar.jpg";

const useStyles = makeStyles((theme) => ({
  appbar: {
    background: "#222",
    margin: 0,
  },
  arrow: {
    color: "tomato",
  },
  title: {
    color: "tan",
    flexGrow: "1",
  },
  menuSliderContainer: {
    width: 250,
    background: "#511",
    height: "100%",
  },
  avatar: {
    display: "block",
    margin: "0.5rem auto",
    width: theme.spacing(13),
    height: theme.spacing(13),
  },
  listItem: {
    color: "tan",
  },
}));

const menuItems = [
  { listIcon: <Home />, listText: "Home", listPath: "/" },
  { listIcon: <AssignmentInd />, listText: "About Me", listPath: "/resume" },
  { listIcon: <Apps />, listText: "Portfolio", listPath: "/portfolio" },
  { listIcon: <ContactMail />, listText: "Contact", listPath: "/contact" },
];

const Navbar = () => {
  const [open, setOpen] = useState(false);

  const classes = useStyles();

  const sideList = () => (
    <Box className={classes.menuSliderContainer} component="div">
      <Avatar className={classes.avatar} src={avatar} alt="Sanjana Srinivas" />
      <Divider />
      <List>
        {menuItems.map((item, i) => (
          <ListItem
            button
            key={i}
            className={classes.listItem}
            onClick={() => setOpen(false)}
            component={Link}
            to={item.listPath}
          >
            <ListItemIcon className={classes.listItem}>
              {item.listIcon}
            </ListItemIcon>
            <ListItemText primary={item.listText} />
          </ListItem>
        ))}
        <ListItem
            button
            className={classes.listItem}
            onClick={() => {
              setOpen(false);
              window.open(require('../documents/resume_sanjana.pdf'));
            }}
          >
            <ListItemIcon className={classes.listItem}>
              <SubjectIcon />
            </ListItemIcon>
            <ListItemText primary="Resume" />
          </ListItem>
      </List>
    </Box>
  );

  const drawer = () => (
    <Drawer open={open} anchor="right" onClose={() => setOpen(false)}>
        {sideList()}
    </Drawer>
  );

  return (
    <React.Fragment>
      <Box component="nav">
        <AppBar position="fixed" className={classes.appbar}>
          <Toolbar>
            <IconButton onClick={() => setOpen(true)}>
              <MenuIcon className={classes.arrow} />
            </IconButton>
            <Typography variant="h5" className={classes.title}>
              SS
            </Typography>
            <div className={classes.listItem}>
              <Button color="inherit" component={Link} to="/">Home</Button>
              <Button color="inherit" component={Link} to="/resume">About Me</Button>
              <Button color="inherit" component={Link} to="/portfolio">Portfolio</Button>
              <Button color="inherit" component={Link} to="/contact">Contact</Button>
              <Button color="inherit" onClick={() => window.open(require("../documents/resume_sanjana.pdf"))}>Resume</Button>
            </div>
          </Toolbar>
        </AppBar>
      </Box>
      {drawer()}
    </React.Fragment>
  );
};

export default Navbar;
